import { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './Stroy.module.css';

gsap.registerPlugin(ScrollTrigger);

function Story() {
  const txtBoxRefs = useRef([]);

  useEffect(() => {
    txtBoxRefs.current.forEach((text, index) => {
      gsap.fromTo(
        text,
        { y: -50, opacity:0 },
        {
          y: 0,
          opacity: 1,
          ease:'power3.out',
          scrollTrigger: {
            trigger: text,
            start: 'top 60%',
            end: 'top 70%',
            scrub: false,
            once: true,
          },
        }
      );
    });
  }, []);

  return (
    <div className={styles.txtBox}>
      <h3 ref={(el)=> el && txtBoxRefs.current.push(el)}>Healthy But Awesome Flavors</h3>
      <p ref={(el)=> el && txtBoxRefs.current.push(el)}>
        HBAF는 <strong>국내 최대 견과류 브랜드</strong>로, <strong>40년간의 경험</strong>과 혁신을 바탕으로
        국내외 시장에서 성장해왔습니다.
      </p>
      <p ref={(el)=> el && txtBoxRefs.current.push(el)}>
        <strong>국내 최초-최대의 견과류 전문 회사</strong>에서 종합 식품 기업으로 도약하며 새로운 역사를
        만들어가고 있습니다.
      </p>
    </div>
  );
}

export default Story;
