import Arrow from './Arrow';
import styles from './Product.module.css';

function Product() {
  const path = process.env.PUBLIC_URL;
  return (
    <div className={styles.productsImg}>
          <div className={`${styles.card} flex`}>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img1.png"} alt="starlight" />
              <div className={`${styles.listTitle} flex`}>
                <p>starlight x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img2.png"} alt="starlight" />
              <div className={`${styles.listTitle} flex`}>
                <p>coconut x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img3.png"} alt="starlight" />
              <div className={`${styles.listTitle} flex`}>
                <p>injeolmi x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
          </div>

          <div className={`${styles.card} flex`}>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img4.png"} alt="후라이드치킨맛아몬드" />
              <div className={`${styles.listTitle} flex`}>
                <p>seoul x hbaf</p>
                <p>< Arrow/></p>
              </div>
            </div>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img5.png"} alt="양념치킨맛아몬드" />
              <div className={`${styles.listTitle} flex`}>
                <p>seoul x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img6.png"} alt="간장치킨맛아몬드" />
              <div className={`${styles.listTitle} flex`}>
                <p>seoul x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
          </div>

          <div className={`${styles.card} flex`}>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img7.png"} alt="seaweed" />
              <div className={`${styles.listTitle} flex`}>
                <p>seaweed x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img8.png" }alt="sichuan" />
              <div className={`${styles.listTitle} flex`}>
                <p>sichuan x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
            <div className={`${styles.cardList} flex`}>
              <img src={path + "./images/new-img9.png"} alt="anchovy" />
              <div className={`${styles.listTitle} flex`}>
                <p>anchovy x hbaf</p>
                <p><Arrow/></p>
              </div>
            </div>
          </div>
        </div>
  );
}

export default Product;