import React from 'react';
import styles from './MenuBtn.module.css';

// 메뉴 링크 컴포넌트를 먼저 정의
class MenuLinks extends React.Component {
  render() {
    const links = [
      { text: 'HBAF' },
      { text: 'PRODUCTS' },
      { text: 'STORY' },
      { text: 'CONTACT' },
    ];

    const menuClass = this.props.isOpen ? `${styles.menu} ${styles.open}` : styles.menu;

    return (
      <ul className={menuClass}>
        {links.map((link, i) => (
          <li key={i}>
            <span >{link.text}</span>
          </li>
        ))}
      </ul>
    );
  }
}

// MenuBtn 컴포넌트 정의
class MenuBtn extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isOpen: false };
    this.menuContainer = React.createRef();
  }

  componentDidMount() {
    document.addEventListener('click', this._handleDocumentClick);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this._handleDocumentClick);
  }

  _menuToggle = (e) => {
    e.stopPropagation();
    this.setState((prevState) => ({ isOpen: !prevState.isOpen }));
  };

  _handleDocumentClick = (e) => {
    if (
      this.state.isOpen &&
      this.menuContainer.current &&
      !this.menuContainer.current.contains(e.target)
    ) {
      this.setState({ isOpen: false });
    }
  };

  render() {
    const menuStatus = this.state.isOpen ? styles.open : '';

    return (
      <menuWrapper className={`${styles.header} ${menuStatus} ${styles.menuWrapper}`} ref={this.menuContainer}>
        <div className={styles.icon} onClick={this._menuToggle}>
          <span className={styles.bar1}></span>
          <span className={styles.bar2}></span>
          <span className={styles.bar3}></span>
        </div>
        {/* isOpen 상태를 MenuLinks에 전달 */}
        <MenuLinks isOpen={this.state.isOpen} />
      </menuWrapper>
    );
  }
}

export default MenuBtn;
