import styles from './Logos.module.css';

function Logos() {
  const path = process.env.PUBLIC_URL;
  return (
    <div className={styles.logos}>
      <img src={path + "./images/logos1.png"} alt="logo1" />
      <img src={path + "./images/logos2.png"} alt="logo2" />
      <img src={path + "./images/logos3.png"} alt="logo3" />
      <img src={path + "./images/logos4.png"} alt="logo4" />
      <img src={path + "./images/logos5.png"} alt="logo5" />
    </div>
  );
}

export default Logos;