import styles from './Footer.module.css';

function Footer() {
  const path = process.env.PUBLIC_URL; 
  return (
    <footer>
    <h2 class={styles.footerLogo}>
      <img src={path + "./images/logo.png"} alt="footer logo"/>
    </h2>
    <div class={styles.address}>
      <p>광주본사  |  경기도 광주시 봉골길 176 (문형동)</p>
      <p>서울사무소  |   서울특별시 강남구 도산대로 45길 6, 3층 (신사동) </p>
      <p>원주공장  |   강원도 원주시 지정면 신지정로 39 (신평리)</p>
      <p>hbaf@hbaf.co.kr</p>
    </div>
    <div class={styles.copyrights}>
      <p>Copyrights 2023 HBAF All Rights Reserved</p>
    </div>
    <div class={`${styles.footerBottom} flex`}>
      <div class={styles.footerLeft}>
        <h4>CATEGORY</h4>
        <p>ABOUT</p>
        <p>PRODUCTS</p>
        <p>FLAVORS</p>
        <p>STORY</p>
        <p>CONTACT</p>
      </div>
      <div class={styles.footerRight}>
        <h4>CS CENTER</h4>
        <h4>080-700-8383</h4>
        <p>주중 오전9시 - 오후 5시</p>
        <p> <a href="https://sihyun96.github.io/sihyun-portfolio/" target='blank'>포트폴리오 바로가기</a>
        <a href="https://github.com/sihyun96/241216-hbaf.git" target='blank'>github 바로가기</a></p>
      </div>
    </div>
  </footer>
  );
}

export default Footer;