import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './MainTxt.module.css';

gsap.registerPlugin(ScrollTrigger);

function MainTxt() {
  const textRefs = useRef([]); // 각 h3 요소를 참조

  useEffect(() => {
    textRefs.current.forEach((text) => {
      gsap.fromTo(
        text,
        { y: -50, opacity: 0 }, // 초기 상태: 위로 올라가 있고 투명
        {
          y: 0, // 최종 상태: 원래 위치
          opacity: 1, // 완전히 보이게
          duration: 1, // 애니메이션 지속 시간
          ease: 'power3.out', // 부드러운 애니메이션
          scrollTrigger: {
            trigger: text, // 트리거가 되는 요소
            start: 'top 90%', // 화면의 90% 지점에서 애니메이션 시작
            end: 'top 70%', // 70% 지점에서 애니메이션 종료
            scrub: true, // 부드러운 동작
            once: false, // 애니메이션을 한 번만 실행하지 않음
            toggleActions: 'play none none reverse', // 스크롤 방향에 따라 재생/역재생
          },
        }
      );
    });
  }, []);

  return (
    <>
      <h1 className={styles.logo}>
        <img src="./images/logo.svg" alt="로고" />
      </h1>
      <div className={styles.mainTxtWrapper}>
        <h3
          className={styles.mainStroke}
          data-content="“Korea’s Largest"
          ref={(el) => el && textRefs.current.push(el)}
        >
          “Korea’s Largest
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="Nut Brand”"
          ref={(el) => el && textRefs.current.push(el)}
        >
          Nut Brand”
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="HBAP, H is silent"
          ref={(el) => el && textRefs.current.push(el)}
        >
          HBAP, H is silent
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="My best friend,"
          ref={(el) => el && textRefs.current.push(el)}
        >
          My best friend,
        </h3>
        <h3
          className={styles.mainStroke}
          data-content="My HBAF"
          ref={(el) => el && textRefs.current.push(el)}
        >
          My HBAF
        </h3>
      </div>
    </>
  );
}

export default MainTxt;
