import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import styles from './Global.module.css';

gsap.registerPlugin(ScrollTrigger);

function Global() {
  const path = process.env.PUBLIC_URL;
  const textRefs = useRef([]);
  useEffect(() => {
      textRefs.current.forEach((text, index) => {
        gsap.fromTo(
          text,
          { y: -50, opacity: 0 }, // 초기 상태: 위로 올라가 있고 투명
          {
            y: 0, // 최종 상태: 원래 위치
            opacity: 1, // 완전히 보이게
            duration: 1, // 애니메이션 지속 시간
            ease: 'power3.out', // 부드러운 애니메이션
            scrollTrigger: {
              trigger: text, // 트리거가 되는 요소
              start: 'top 90%', // 화면의 90% 지점에서 애니메이션 시작
              end: 'top 70%', // 70% 지점에서 애니메이션 종료
              scrub: false, // 스크롤에 따라 부드럽게 동작하지 않음 (고정된 애니메이션)
              once: true, // 애니메이션이 한 번만 실행됨
            },
          }
        );
      });
    }, []);

  return (
    <div className={styles.global}>
      <p ref={(el) => el && textRefs.current.push(el)}>HBAF는 2015년 첫 수출을 시작으로 현재 <strong>세계 25개국</strong>에 수출하고 있으며 해외 <strong>시장의 판로를 확대</strong>하고 있습니다</p>
      <img className={styles.map} src={path + "./images/global-img.png"} alt="지도" ref={(el) => el && textRefs.current.push(el)} />
    </div>
  );
}

export default Global;