import Global from "../components/Global";
import Logos from "../components/Logos";
import MainImg from "../components/MainImg";
import MainTxt from "../components/MainTxt";
import Product from "../components/Product";
import Story from "../components/Story";
import Value from "../components/Value";
import styles from "./Contans.module.css";

function Contant() {
  const path = process.env.PUBLIC_URL;

  return (
    <div className={styles.content}>
      <section className={styles.main}>
        < MainTxt/>
        < MainImg/>
        <img className={styles.bee} src= {path + "./images/main-img.png"} alt="아몬드 꿀벌 캐릭터" />
      </section>

      <section className={styles.story}>
        <h2 className="title stroke">story</h2>
        < Story />
      </section>

      <section className={styles.value}>
        <h2 className="title stroke">Our Value</h2>
        < Value/>
      </section>

      <section className={styles.products}>
        <h2 className="title stroke">our products</h2>
        < Product/>
      </section>

      <section className={styles.global}>
        <h2 className="title stroke">Global Expansion</h2>
        < Global/>
      </section>

      <section className={styles.logos}>
        <Logos/>
      </section>

      <section className={styles.contact}>
        <h2 className="title stroke">contact</h2>
        <div className={`${styles.contactWrapper} flex`}>
          <div className={`mail ${styles.mail1}`}>
            <p>온/오프라인 입점 문의</p>
            <p>hbaf@hbaf.co.kr</p>
          </div>
          <div className={`mail ${styles.mail2}`}>
            <p>해외 입점 문의</p>
            <p>export@hbaf.co.kr</p>
          </div>
        </div>
        <div className={`${styles.contactWrapper} flex`}>
          <div className={`mail ${styles.mail3}`}>
            <p>마케팅,제휴 관련</p>
            <p>marketing@hbaf.co.kr</p>
          </div>
          <div className={`mail ${styles.mail4}`}>
            <p>제품,품질 문의</p>
            <p>cs@hbaf.co.kr</p>
          </div>
        </div>
      </section>

      <section class="btn">
      view more
    </section>
    </div>
  );
}
export default Contant;